.container {
  height: 100vh;
  display: grid;
  grid-template-columns: 0.6fr repeat(2, 1fr) 0.6fr;
  grid-template-rows: 0.2fr repeat(3, 1fr) 0.4fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

/* header */
.header {
  grid-area: 1 / 1 / 2 / 6;
  background-color: antiquewhite;
}

.header .container__header .header-nav {
  display: flex;
  gap: 10px;
}

.header .container__header a {
  color: black;
  text-decoration: none;
}

/* side kiri */
.side-left {
  grid-area: 2 / 1 / 5 / 2;
  background-color: salmon;
}

.side-left .container__side-left {
}

/* konten */
.content {
  grid-area: 2 / 2 / 5 / 4;
  background-color: darkcyan;
}

.content .container__content {
}

/* side kanan */
.side-right {
  grid-area: 2 / 4 / 5 / 5;
  background-color: goldenrod;
}

.side-right .container__side-right {
}

/* footer */
.footer {
  grid-area: 5 / 1 / 6 / 5;
  background-color: brown;
}

.footer .container__footer {
}
